.questionnaire .hexagon {
  width: 100px;
  height: 55px;
  position: relative;
  left: 28%;
  bottom: -100px;
  z-index: -1;
  background-color: #184E58 !important;
}

.questionnaire .hexagon, 
.questionnaire .hexagon:before, 
.questionnaire .hexagon:after {
  background: #184E58;
  box-shadow: 0 0 10px rgba(0,0,0,0.8);   
}

.questionnaire .hexagon:before,
.questionnaire .hexagon:after {
  content: "";
  position: absolute;
  left: 22px;
  width: 57px;
  height: 57px;
  transform: rotate(145deg) skew(22.5deg);
}

.questionnaire .hexagon:before {
  top: -29px;
}

.questionnaire .hexagon:after {
  top: 27px;
}

.questionnaire .hexagon span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 55px;
  background: #184E58;
  z-index: 1;
}

.questionnaire .chosen_one {
    animation: action 0.3s linear alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes action {
    0% { 
        transform: translateY(0); 
        transition-timing-function: ease-in-out;
    }
    100% { 
        transform: translateY(-25px); 
        transition-timing-function: ease-in-out;
    }
}

